html {
  font-size: 62.5%;
  display: block;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #eceae1;
}

p {
  color: #1b1b1b;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 140rem;
  margin: 0 auto;
}

.contenedor-contenido {
  width: 95%;
  max-width: 110rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 1.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 1.8rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: white;
  font-weight: 700;
  text-transform: uppercase;
  max-width: 80rem;
  margin: 1rem;
}
@media (min-width: 768px) {
  .alerta {
    margin: 1rem auto;
  }
}
.alerta.error {
  background-color: red;
}
.alerta.exito {
  background-color: green;
}

.up {
  display: none;
}
@media (min-width: 992px) {
  .up {
    position: fixed;
    bottom: 40px;
    right: 40px;
    transition: all 300ms ease 0ms;
    z-index: 99;
    filter: invert(30%);
  }
}

.show {
  display: block;
}

/** Media Queries **/
.header {
  background-color: #eceae1;
  margin: 0;
  border-bottom: 1px solid #babbb6;
}
@media (min-width: 768px) {
  .header {
    border-bottom: none;
  }
}
.header .logo {
  font-size: 5rem;
  font-family: "Roadam";
  color: #1b1b1b;
  margin-left: 1.5rem;
}
@media (min-width: 768px) {
  .header .logo {
    margin-left: unset;
  }
  .header .logo:hover {
    transform: scale(1.05);
    transition: transform 0.5s ease;
  }
}

.logo-barra {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .logo-barra {
    width: unset;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.barra a {
  font-size: 1.6rem;
  font-weight: 700;
  color: #1b1b1b;
  margin-bottom: 1.5rem;
}
.barra a:hover {
  color: #6c746c;
}
@media (min-width: 768px) {
  .barra a {
    margin-bottom: 0;
    margin-right: 1.2rem;
    font-weight: 700;
  }
  .barra a:last-of-type {
    margin-right: 0;
  }
}
.barra a.logo {
  margin-bottom: 0;
  font-weight: 400;
}
.barra .mobile-menu img {
  height: 4rem;
  width: 4rem;
}
@media (min-width: 768px) {
  .barra .mobile-menu {
    display: none;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: scaleY(0);
  visibility: hidden;
  opacity: 0;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    opacity: 1;
    visibility: visible;
    padding-bottom: 3rem;
    transform: none;
  }
}

.mostrar {
  transform-origin: top;
  transition: transform 0.26s ease;
  transform: scaleY(1);
  visibility: visible;
  opacity: 1;
  height: auto;
}

.inicio {
  background-color: #eceae1;
}
@media (min-width: 768px) {
  .inicio {
    min-height: calc(100vh - 90px);
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}
.inicio .contenido-inicio {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .inicio .contenido-inicio {
    display: flex;
    flex-direction: row;
  }
}
.inicio .texto {
  padding-top: 2rem;
  line-height: 1;
}
.inicio .texto h1 {
  margin-top: 0;
  margin-left: 0;
  font-weight: 900;
  font-size: 2rem;
  color: #1b1b1b;
}
.inicio .texto span {
  font-size: 5rem;
  color: #1b1b1b;
}
.inicio .texto h2 {
  font-size: 2rem;
  margin-left: 0;
  text-align: center;
  font-weight: 900;
  color: #6c746c;
}
.inicio .texto p {
  line-height: 1.5;
  text-align: center;
}
@media (min-width: 768px) {
  .inicio .texto h1 {
    text-align: left;
    margin-bottom: 0;
  }
  .inicio .texto span {
    font-size: 10rem;
    text-align: left;
  }
  .inicio .texto h2 {
    font-size: 4rem;
    text-align: left;
    margin-top: 0;
  }
  .inicio .texto p {
    text-align: left;
  }
}
.inicio .texto .redes {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
}
@media (min-width: 768px) {
  .inicio .texto .redes {
    justify-content: flex-start;
    gap: 2.5rem;
  }
  .inicio .texto .redes lottie-player:hover {
    transform: scale(1.3);
  }
}
.inicio .texto .centrar {
  margin-top: 1rem;
  text-align: center;
}
.inicio .texto .centrar a.boton {
  background-color: #363a36;
  color: white;
  font-weight: 700;
  text-decoration: none;
  padding: 1.5rem 3rem;
  border-radius: 0.5rem;
  text-align: center;
  display: inline-block;
  border: none;
}
.inicio .texto .centrar a.boton:hover {
  background-color: #4f544f;
  cursor: pointer;
}
@media (min-width: 768px) {
  .inicio .texto .centrar {
    text-align: left;
    padding: 0;
  }
  .inicio .texto .centrar a.boton {
    background-color: #363a36;
    color: white;
    font-weight: 700;
    text-decoration: none;
    padding: 1.5rem 3rem;
    border-radius: 0.5rem;
    text-align: center;
    display: inline-block;
    border: none;
  }
  .inicio .texto .centrar a.boton:hover {
    background-color: #4f544f;
    cursor: pointer;
  }
}

.sobre-mi {
  display: flex;
  align-items: center;
  background-color: #fbfbfb;
}
@media (min-width: 992px) {
  .sobre-mi {
    min-height: 100vh;
  }
}
@media (min-width: 768px) {
  .sobre-mi .contenido-sobremi {
    display: flex;
    flex-direction: row;
  }
  .sobre-mi .contenido-sobremi img {
    margin: 0 auto;
    max-width: 50rem;
    max-height: 50rem;
  }
}
.sobre-mi .contenido-sobremi .texto-sobremi {
  padding: 0 1rem;
}
@media (min-width: 992px) {
  .sobre-mi .contenido-sobremi .texto-sobremi {
    padding: 1rem;
  }
  .sobre-mi .contenido-sobremi .texto-sobremi p {
    padding: 1rem;
  }
}
.sobre-mi .contenido-sobremi h1 {
  padding-top: 1rem;
  margin-top: 1rem;
  font-weight: 900;
  font-size: 4rem;
  color: #1b1b1b;
}
.sobre-mi .contenido-sobremi h2 {
  font-size: 2rem;
  margin-left: 0;
  margin-bottom: 0;
  text-align: center;
  font-weight: 900;
  color: #414641;
}

.tecnologias {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 1rem;
}
.tecnologias .tecnologia img {
  width: 5rem;
  height: 5rem;
  margin: 0 auto;
}
.tecnologias .tecnologia h3 {
  color: #1b1b1b;
  font-size: 1.8rem;
  font-weight: bold;
  text-align: center;
}

.servicios {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  background-color: #1a1a1a;
}
@media (min-width: 992px) {
  .servicios {
    min-height: 100vh;
  }
}
.servicios h1 {
  font-weight: 900;
  font-size: 4rem;
  color: #9297a5;
}
.servicios .contenido-servicios {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
}
.servicios .contenido-servicios:last-of-type {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .servicios .contenido-servicios {
    flex-direction: row;
    gap: 2rem;
  }
}
.servicios .contenido-servicios .servicio {
  background-color: #232325;
  border-radius: 8px;
  padding: 3rem;
  margin: 1rem;
}
@media (min-width: 768px) {
  .servicios .contenido-servicios .servicio:hover {
    transform: scale(1.05);
    transition: transform 0.5s ease;
  }
}
.servicios .contenido-servicios .servicio .titulo {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: space-between;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}
.servicios .contenido-servicios .servicio .titulo h2 {
  font-size: 2rem;
  margin: 0;
  font-weight: 900;
  color: #9297a5;
}
.servicios .contenido-servicios .servicio .titulo img {
  width: 4rem;
  height: 4rem;
}
.servicios .contenido-servicios .servicio p {
  margin: 0;
  font-size: 1.8rem;
  color: #9297a5;
}
.servicios .contenido-servicios .servicio p span {
  font-weight: 900;
  color: #9297a5;
}

.contacto {
  background-color: #eeeeee;
  padding-bottom: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 992px) {
  .contacto {
    min-height: 100vh;
    padding-bottom: unset;
  }
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto {
    min-width: 75rem;
  }
}
.contacto .contenedor-contacto .titulo-contacto {
  text-align: center;
  margin: 0 auto;
  padding-top: 1rem;
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto .titulo-contacto {
    max-width: 60rem;
  }
}
.contacto .contenedor-contacto .titulo-contacto h1 {
  font-weight: 900;
  font-size: 4rem;
  color: #1b1b1b;
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto .titulo-contacto h1 {
    font-size: 5rem;
  }
}
.contacto .contenedor-contacto .titulo-contacto p {
  margin: 0;
  font-size: 1.9rem;
  padding: 0 1rem;
  color: #363a36;
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto .titulo-contacto p {
    font-size: 3rem;
  }
}
.contacto .contenedor-contacto .formulario {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto .formulario {
    margin-top: 5rem;
  }
}
.contacto .contenedor-contacto .formulario label {
  color: #6c746c;
  font-size: 13px;
  margin: 0 0 0 11px;
  font-weight: 200;
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto .formulario label {
    font-size: 13px;
  }
}
.contacto .contenedor-contacto .formulario input:not([type=submit]),
.contacto .contenedor-contacto .formulario textarea {
  padding: 1rem;
  margin-bottom: 3rem;
  border: 0;
  font-size: 18px;
  line-height: 1.5;
  color: #6c746c;
  border-bottom: 1px solid #6c746c;
  background-color: #eeeeee;
  font-weight: 400;
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto .formulario input:not([type=submit]),
.contacto .contenedor-contacto .formulario textarea {
    min-width: 35rem;
  }
}
.contacto .contenedor-contacto .formulario input:not([type=submit]):focus::placeholder,
.contacto .contenedor-contacto .formulario textarea:focus::placeholder {
  color: transparent;
}
.contacto .contenedor-contacto .formulario input:not([type=submit]):focus,
.contacto .contenedor-contacto .formulario textarea:focus {
  outline: none;
}
.contacto .contenedor-contacto .formulario textarea {
  min-height: 13rem;
  resize: none;
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto .formulario textarea {
    min-height: 10rem;
  }
}
.contacto .contenedor-contacto .formulario .nombre-correo {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contacto .contenedor-contacto .formulario .nombre-correo {
    flex-direction: row;
    justify-content: space-between;
  }
}
.contacto .contenedor-contacto .formulario .nombre-correo .nombre, .contacto .contenedor-contacto .formulario .nombre-correo .correo {
  display: flex;
  flex-direction: column;
  padding: 0 1.5rem;
}
.contacto .contenedor-contacto .formulario .mensaje {
  display: flex;
  flex-direction: column;
  padding: 0 1.5rem;
}
.contacto .contenedor-contacto .formulario input.boton {
  background-color: #363a36;
  color: white;
  font-weight: 700;
  text-decoration: none;
  padding: 1.5rem 3rem;
  border-radius: 0.5rem;
  text-align: center;
  display: block;
  border: none;
  margin: 0 auto;
}
.contacto .contenedor-contacto .formulario input.boton:hover {
  background-color: #4f544f;
  cursor: pointer;
}

.footer {
  background-color: #1b1b1b;
  margin: 0;
}
.footer .contenido-footer {
  text-align: center;
  border-bottom: 1px solid #414641;
  padding: 1rem 0;
}
.footer .contenido-footer a {
  color: white;
  margin-right: 1rem;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .footer .contenido-footer a {
    font-size: 1.6rem;
  }
}
.footer .contenido-footer a:hover {
  color: #6c746c;
}
.footer .contenido-footer a:last-of-type {
  margin-right: 0;
}
@media (min-width: 768px) {
  .footer .contenido-footer {
    text-align: left;
  }
}
.footer .copy-redes {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .footer .copy-redes {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer .copy-redes .copyright {
  text-align: center;
  font-size: 1.5rem;
  text-align: center;
  color: white;
  padding: 1rem 0;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .footer .copy-redes .copyright {
    padding: 0 0 1rem 0;
  }
}
.footer .copy-redes .footer-redes {
  display: flex;
